.search-div {
    width: 100%;
    height: 30px;
    position: relative;
    background-image: url("data:image/png;base64,");
    background-repeat: no-repeat;
    background-size: 3%;
    background-position: 10px;
    background-color: rgb(255, 133, 42);
    text-align: center;
    line-height: 30px;
    padding-top: 5px;
    padding-bottom: 5px;
}
.search-div span {
    width: 20px;
    color:#d5dddd;
}
.icon-search {
    width: 15px;
    height: 15px;
    padding-top: 0px;
}
.contain {
    margin-left: 10%;
    margin-right: 10%;
    background-color: #ff4822;
    border-radius: 5px;
}