.casel {
    width: 100%;
    height: 360px;
}
.container {
    padding: 24px;
    background-color:lightyellow
}
.detail-container{
    background-color: rgb(242, 242, 242);
}
.detail {
    width: 36%;
    height: auto;
    padding: 5%;
    margin: 2% 2% 8% 2%;
    background-color: #fff;
    border-radius: 5px;
    float: left;
}
.detail div {
    font-size: 20px;
}
.detail img{
    width: 100%;
    height: 200px;
    border-radius: 8px;
}
.detail p {
    font-size: 30px;
    color: orange;
}